import React, { useState, useEffect } from 'react'
import { ResponsivePie } from '@nivo/pie'
import { numberWithCommas } from '../../../utils/lib/Regex';

const PieChart = ({ data1, data2, ...props }) => {

  const [ state, setState ] = useState(0);
  const [ dataDisplay, setDataDisplay ] = useState('');

  const [ dataA, setDataA ] = useState('')
  const [ dataB, setDataB ] = useState('')

  useEffect(() => {
    if (data1 && data2) {
      // data1 value 합계
      const data1Sum = data1?.reduce((acc, cur) => {
        return acc + cur.value;
      }, 0);
  
      // data2 value 합계
      const data2Sum = data2?.reduce((acc, cur) => {
        return acc + cur.value;
      }, 0);
  
      // data1 value의 합계의 퍼센트 값으로 변경하고 formattedValue에 저장
      const tempA = data1?.map((item) => {
          return {...item, formattedValue: (item.value / data1Sum) * 100}
        }
      );
      setDataA(tempA)
  
      // data2 value의 합계의 퍼센트 값으로 변경하고 formattedValue에 저장
      const tempB = data2?.map((item) => {
          return {...item, formattedValue: (item.value / data2Sum) * 100}
        }
      );
      setDataB(tempB)
    }

  }, [data1, data2]);



    const clickDisplay = (data) => {
      setDataDisplay(
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
          }}>
          <div
            style={{
              fontSize: '12px',
              fontWeight: 'bold',
              color: '#000000',
            }}>
            {numberWithCommas(data.value)}명
          </div>
          <div
            style={{
              fontSize: '10px',
              fontWeight: 'bold',
              color: '#777777',
            }}>
            {data.id}
          </div>
        </div>
      );
    }


  const handle = {
    pieClick: (data) => {
      return clickDisplay(data);
    },

    legendClick: (data) => {
      return clickDisplay(data);
    },
  };


  return (
    <>
      <div
        style={{
          width: '440px',
          height: '320px',
          margin: '0 auto',
          position: 'relative',
        }}
      >
        <div
          onClick={() => {
            setState(0);
            setDataDisplay('');
          }}
          style={{
            color: state === 0 ? '#000000' : '#AAAAAA',
            fontSize: '16px',
            fontWeight: 'bold',
            position: 'absolute',
            paddingBottom: '5px',
            top: 15,
            left: 20,
            zIndex: 999,
            cursor: 'pointer',
            borderBottom: state === 0 && '2px solid #2F5DF2',
          }}
        >
          성별 통계
        </div>
        <div
          onClick={() => {
            setState(1);
            setDataDisplay('');
          }}
          style={{
            color: state === 1 ? '#000000' : '#AAAAAA',
            fontSize: '16px',
            fontWeight: 'bold',
            position: 'absolute',
            paddingBottom: '5px',
            top: 15,
            left: 100,
            zIndex: 999,
            cursor: 'pointer',
            borderBottom: state === 1 && '2px solid #2F5DF2',
          }}
        >
          연령별 통계
        </div>

        {state === 0 && dataA && (
          <>
            <ResponsivePie
              {...props}
              data={dataA}
              margin={{ top: 70, right: 80, bottom: 70, left: 80 }}
              innerRadius={0.65}
              padAngle={3}
              cornerRadius={5}
              // activeOuterRadiusOffset={8}
              borderWidth={1}
              borderColor={{
                from: 'color',
                modifiers: [['darker', 0]],
              }}
              theme={{
                legends: {
                  text: {
                    fontSize: 12,
                    fill: '#000000',
                  },
                },
                labels: {
                  text: {
                    fontSize: 12,
                    fontWeight: 'bold',
                  },
                },
              }}
              colors={['#5E81F4', '#FF8282']}
              arcLinkLabel={function (e) {
                return e.id + ' : ' + e.data?.formattedValue?.toFixed(2) + '%';
              }}
              arcLinkLabelsSkipAngle={false}
              arcLinkLabelsTextColor="#333333"
              arcLinkLabelsThickness={2}
              arcLabelsSkipAngle={99999}
              // arcLinkLabelsDiagonalLength={20}
              arcLinkLabelsStraightLength={20}
              activeInnerRadiusOffset={5}
              activeOuterRadiusOffset={5}
              arcLinkLabelsDiagonalLength={5}
              arcLinkLabelsColor={{ from: 'color' }}
              onClick={handle.pieClick}
              tooltip={(e) => {
                return (
                  <div
                    style={{
                      background: '#FFFFFF',
                      padding: '5px 15px',
                      borderRadius: '4px',
                      boxShadow: '0 0 10px rgba(0, 0, 0, 0.25)',
                      fontSize: '12px',
                      fontWeight: 'bold',
                      color: '#000000',
                    }}
                  >
                    {e.datum.id} : {e.datum.value}명 ({e.datum?.data?.formattedValue?.toFixed(2)}%)
                  </div>
                );
              }}
              legends={[
                {
                  anchor: 'bottom',
                  direction: 'row',
                  justify: false,
                  translateX: -120,
                  translateY: 55,
                  itemsSpacing: 5,
                  itemWidth: 80,
                  itemHeight: 30,
                  itemTextColor: '#000000',
                  // itemBackground: '#EEEEEE',
                  itemDirection: 'left-to-right',
                  itemOpacity: 1,
                  symbolSize: 16,
                  symbolShape: 'circle',
                  effects: [
                    {
                      on: 'hover',
                      style: {
                        itemOpacity: 0.75,
                      },
                    },
                  ],
                  onClick: handle.legendClick,
                },
              ]}
            />
            <div
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 999,
                pointerEvents: 'none',
              }}
            >
              {dataDisplay}
            </div>
          </>
        )}

        {state === 1 && dataB && (
          <>
            <ResponsivePie
              {...props}
              data={dataB}
              margin={{ top: 70, right: 80, bottom: 70, left: 80 }}
              innerRadius={0.65}
              padAngle={3}
              cornerRadius={5}
              // activeOuterRadiusOffset={8}
              borderWidth={1}
              borderColor={{
                from: 'color',
                modifiers: [['darker', 0]],
              }}
              theme={{
                legends: {
                  text: {
                    fontSize: 12,
                    fill: '#000000',
                  },
                },
                labels: {
                  text: {
                    fontSize: 12,
                    fontWeight: 'bold',
                  },
                },
              }}
              arcLinkLabelsSkipAngle={false}
              arcLinkLabelsTextColor="#333333"
              arcLinkLabelsThickness={2}
              arcLabelsSkipAngle={99999}
              activeInnerRadiusOffset={5}
              activeOuterRadiusOffset={5}
              colors={['#FF8282', '#FFD37C', '#95FFB2', '#69F6FF', '#95B2FF']}
              arcLinkLabel={function (e) {
                return e.id + ' : ' + e.data?.formattedValue?.toFixed(2) + '%';
              }}
              arcLinkLabelsDiagonalLength={5}
              arcLinkLabelsColor={{ from: 'color' }}
              tooltip={(e) => {
                return (
                  <div
                    style={{
                      background: '#FFFFFF',
                      padding: '5px 15px',
                      borderRadius: '4px',
                      boxShadow: '0 0 10px rgba(0, 0, 0, 0.25)',
                      fontSize: '12px',
                      fontWeight: 'bold',
                      color: '#000000',
                    }}
                  >
                    {e.datum.id} : {e.datum.value}명 ({e.datum?.data?.formattedValue?.toFixed(2)}%)
                  </div>
                );
              }}
              onClick={handle.pieClick}
              legends={[
                {
                  anchor: 'bottom',
                  direction: 'row',
                  justify: false,
                  translateX: 10,
                  translateY: 55,
                  itemsSpacing: 5,
                  itemWidth: 80,
                  itemHeight: 30,
                  itemTextColor: '#000000',
                  // itemBackground: '#EEEEEE',
                  itemDirection: 'left-to-right',
                  itemOpacity: 1,
                  symbolSize: 16,
                  symbolShape: 'circle',
                  effects: [
                    {
                      on: 'hover',
                      style: {
                        itemOpacity: 0.75,
                      },
                    },
                  ],
                  onClick: handle.legendClick,
                },
              ]}
            />
            <div
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 999,
                pointerEvents: 'none',
              }}
            >
              {dataDisplay}
            </div>
          </>
        )}
      </div>
    </>
  );
};

export default PieChart;