import { useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { getAgeStatistic, getGenderStatistic } from '../../../../api/Association/statisticAPI'
import PieChart from '../../../../components/common/NivoCharts/PieChart'
import ChartWrap from '../../../../components/common/NivoCharts/ChartWrap/ChartWrap'
import { useDebug } from '../../../../hooks/basic/useDebug';

const GenderAgeStatistic = ({ year, month, region, ...props}) => {

  // query
  const { data: genderData } = useQuery(['getGenderStatistic', year, month, region], () => getGenderStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      return [
        { id: '남자', value: res.male_count },
        { id: '여자', value: res.female_count },
      ]
    }
  })

  const { data: ageData } = useQuery(['getAgeStatistic', year, month, region], () => getAgeStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      return [
        { id: '미취학', value: res.preschool_count },
        { id: '초등학생', value: res.elementary_count },
        { id: '중학생', value: res.middle_count },
        { id: '고등학생', value: res.high_count },
        { id: '성인', value: res.adult_count },
      ]
    }
  })


  // useDebug(genderData)

  return (
    <>
      <PieChart data1={genderData} data2={ageData}/>
    </>
  )
}

export default GenderAgeStatistic